<template>
	<section>
		<h3 class="title-brand">SONG CA CÙNG THẦN TƯỢNG</h3>

		<carousel :navigationEnabled="true" :autoplay="true" :autoplayHoverPause="true" :perPageCustom="[[375, 3], [480, 3], [768, 3], [1024, 4]]" :paginationEnabled="false" :autoplayTimeout="10000">
			<slide v-for="item, index in items" >
				<router-link :to="{ name : 'detail', params: { id: item.record_id }}" >
					<img :src="'http://media.music.kenh1.vn/' + item.record_thumb" alt="" class="img-thumbnail img_slide">
				</router-link>

				<div class="mt-2">
					<router-link :to="{ name : 'detail', params: { id: item.record_id }}" ><h4>{{ item.song_name }}</h4></router-link>
					<h5 class="singer_name">
						<router-link :to="{ name : 'profile', params: { id: item.user_one_data.user_id }}" >
						{{ item.user_one_data == null ? '' : item.user_one_data.name }} {{ item.user_two_data == null ? '' : ' - ' + item.user_two_data.name }}
						</router-link>
					</h5>
				</div>
				
				<span style="border: 1px solid black; border-radius: 3px; padding: 0.5px 7px;" class="mr-2">
					<v-icon name="heart"></v-icon> {{ item.record_like }}
				</span>
				<span style="border: 1px solid black; border-radius: 3px; padding: 0.5px 7px">
					<v-icon name="headphones"></v-icon> {{ item.record_play }}
				</span>
					
				
			</slide>
		</carousel>

	</section>
</template>

<script>
	import { Carousel, Slide } from 'vue-carousel';
	export default {
		components: { Carousel, Slide },
		data() {
			return {
				items : ''
			}
		},
		created() {
			this.getListData();
		},
		methods: {
			getListData() {
				this.$api.get('newsfeed', {
					
				}).then(res => {
					this.items = res.data.record.data
				}).catch(e => {
					console.log(e)
				})
			}
		}
	}
</script>

<style>
	.VueCarousel-navigation-button {
		top: 30% !important;
	}
</style>