<template>
	<section>
		<h3 class="title-brand">TOP XU HƯỚNG</h3>

		<div class="row">
			
			<div class="col" v-for="item, index in items">
				<router-link :to="{ name : 'detail', params: { id: item.record_id }}" >
					<img v-if="item.record_thumb != null" :src="'http://media.music.kenh1.vn/' + item.record_thumb" alt="" class="img-thumbnail img_slide">
					<img v-else :src="item.user_one_data.avatar" alt="" class="img-thumbnail">
					<div class="mt-2">
						<h4 class="title_item">{{ item.song_name }}</h4>
						<h5 class="singer_name">
							{{ item.user_one_data == null ? '' : item.user_one_data.name }} {{ item.user_two_data == null ? '' : ' - ' + item.user_two_data.name }}
						</h5>
					</div>
				</router-link>

			</div>
									
		</div>

	</section>
</template>

<script>
	export default {
		data() {
			return {
				items : ''
			}
		},
		created() {
			this.getListData();
		},
		methods: {
			getListData() {
				this.$api.get('discover/top-solo', {
					params: {limit : 5}
				}).then(res => {
					this.items = res.data.data.items
				}).catch(e => {
					console.log(e)
				})
			}
		}
	}
</script>