<script>
	import Slide from './Slide'
	import DuetIdol from './DuetIdol'
	import TopMV from './TopMV'
	import TopTrend from './TopTrend'
	import Buumer from './Buumer'

	export default {
		components: {
			'slide' : Slide,
			'duet-idol' : DuetIdol,
			'top-mv' : TopMV,
			'top-trend' : TopTrend,
			'buumer' : Buumer
		},
		data() {
			return {
				items : '',
				list_record : ''
			}
		},
		created() {
			this.getListData();
			this.getListRecordNew()
		},
		methods: {
			getListData() {
				this.$api.get('discover/top-solo', {
					
				}).then(res => {
					this.items = res.data.data.items
				}).catch(e => {
					console.log(e)
				})
			},
			getListRecordNew() {
				this.$api.get('record', {
					params: {all : 1}
				}).then(res => {
					this.list_record = res.data.data.items
				}).catch(e => {
					console.log(e)
				})
			}
		}
	}
</script>