<template>
	<div id="slide_top" class="carousel slide" data-ride="carousel">
		<ol class="carousel-indicators">
			<li data-target="#slide_top" :data-slide-to="index" :class="index == 0 ? 'active' : ''" v-for="item, index in slide_item"></li>
		</ol>
		<div class="carousel-inner">
			<div :class="['carousel-item', index == 0 ? 'active' : '']" v-for="item, index in slide_item">
				<img class="d-block w-100" :src="item.event_image" alt="Second slide">
			</div>
		</div>
		<a class="carousel-control-prev" href="#slide_top" role="button" data-slide="prev">
			<span class="carousel-control-prev-icon" aria-hidden="true"></span>
			<span class="sr-only">Previous</span>
		</a>
		<a class="carousel-control-next" href="#slide_top" role="button" data-slide="next">
			<span class="carousel-control-next-icon" aria-hidden="true"></span>
			<span class="sr-only">Next</span>
		</a>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				slide_item : ''
			}
		},
		created() {
			this.getListData();
		},
		methods: {
			getListData() {
				this.$api.get('event', {
					
				}).then(res => {
					this.slide_item = res.data.data.items
				}).catch(e => {
					console.log(e)
				})
			}
		}
	}
</script>