<template>
    <div class="container">
    	<br>
        <div class="row">
        	<div class="col-md-8 col-xs-12">
        		<video width="100%" height="400" autoplay controls :poster="'http://media.music.kenh1.vn/' + detail.record_thumb">
					<source :src="'http://record.kenh1.vn/record' + detail.record_file" type="video/mp4">
				</video>
				
				<div class="row">
					<div class="col-md-6">
						<h4>{{ detail.song_name }}</h4>
						<router-link :to="{ name : 'profile', params: { id: detail.user_one_data.user_id }}" >
							<h5>{{ detail.user_one_data.name }}</h5>
						</router-link>
					</div>
					<div class="col-md-6">
						<span class="float-right">
							<h5>{{ detail.created_date }}</h5>
							<v-icon name="heart"></v-icon> {{ detail.record_like }}&nbsp;&nbsp;&nbsp;
							<v-icon name="headphones"></v-icon> {{ detail.record_play }}&nbsp;&nbsp;&nbsp;

							<a @click="shareFB()">
								<v-icon name="corner-up-right"></v-icon> Chia sẻ
							</a>
						</span>
					</div>
				</div>

				<hr class="hr_item">
				
				<div class="row">
					<div class="col-md-6" style="display: flex; align-items: center; ">
						<img :src="detail.user_one_data.avatar" alt="" class="img_list rounded-circle avatar_c">
						<span>
							{{ detail.user_one_data.name }} <br>
							<span class="badge badge-secondary">Cấp bậc 1</span>
						</span>
						
					</div>
					<div class="col-md-6" style="display: flex; align-items: center; justify-content: flex-end;">
						<button class="btn btn-follow">Theo dõi</button>
					</div>
				</div>
				
				<hr class="hr_item">

				<h4 class="title-brand">NHỮNG BẢN THU KHÁC</h4>

				<ul class="list-group">
					<li class="list-group-item" v-for="item, index in list_record">
						<div class="row">
							<div class="col-md-8">
								<a @click="select(item.record_id)">{{ item.song_name }}</a>
							</div>
							<div class="col-md-1">
								<span class="float-right">
									<v-icon name="heart"></v-icon>
								</span>
							</div>
							<div class="col-md-1 pd-0">
								{{ item.record_like }}
							</div>
							<div class="col-md-1">
								<span class="float-right">
									<v-icon name="headphones"></v-icon>
								</span>
							</div>
							<div class="col-md-1 pd-0">
								 {{ item.record_play }}
							</div>
						</div>
						<hr class="hr_item">
					</li>

				</ul>

        	</div>
        	<div class="col-md-4">
        		
				<ul v-for="item, index in list_record" class="list-unstyled">
					<li>
						<div class="row">
							<div class="col-sm-4 pd-0">
								<a @click="select(item.record_id)">
			        				<img :src="'http://media.music.kenh1.vn/' + item.record_thumb" alt="" class="img-thumbnail img_list_small">
			        			</a>
							</div>
							<div class="col-sm-8">
								<a @click="select(item.record_id)">
									<span>{{ item.song_name }}</span>
								</a>
									<br>
								<router-link :to="{ name : 'profile', params: { id: item.user_one_data.user_id }}" >	
	        						<small>{{ item.user_one_data.name }}</small>
	        					</router-link>
							</div>
						</div>
						
	        			
					</li>
				</ul>

        	</div>
        </div>
    </div>
</template>

<script>
	export default {
		data() {
			return {
				detail : '',
				list_record : ''
			}
		},
		created() {
			this.getDetail(this.$route.params.id);
			this.getListData()
		},
		methods: {
			getDetail(id) {
				this.$api.get('record/' + id, {
					
				}).then(res => {
					this.detail = res.data.data
				}).catch(e => {
					console.log(e)
				})
			},
			getListData() {
				this.$api.get('discover/top-mv', {
					params: {
						limit : 9
					}
				}).then(res => {
					this.list_record = res.data.data.items
				}).catch(e => {
					console.log(e)
				})
			},
			select(id) {
				var currentUrl = window.location.origin
				window.location.href = currentUrl + '/detail/' + id
			},
			shareFB() {
				var fullURL = window.location.origin + this.$route.fullPath
				window.open('http://www.facebook.com/sharer.php?u=' + fullURL,'_blank');
			}
		}
	}
</script>