<template>
	<div>
		<div class="p-3 p-md-5 text-white bg-gradient">

			<div class="container">
				<div class="row">
					<div class="col-md-4 px-0">
						<center>
							<img :src="detail.avatar" class="rounded-circle img-thumbnail" style="width:200px; height:200px"><br><br>
							<h4 >{{ detail.name }}</h4>
							<h5 >Cấp độ {{ detail.level }}</h5>
						</center>		
						
					</div>
					<div class="col-md-8" style="padding: 5% 0;">
						<div class="row">
							<div class="col-md-4">
								<h4><center>{{ !detail.record ? 0 : detail.record }} <br> bản thu</center></h4>
							</div>
							<div class="col-md-4">
								<h4><center>{{ !detail.data_follower.total ? 0 : detail.data_follower.total }} <br> người theo dõi</center></h4>
							</div>
							<div class="col-md-4">
								<h4><center>{{ !detail.data_following.total ? 0 : detail.data_following.total }} <br> đang theo dõi</center></h4>
							</div>
						</div>
						<br>
						<div class="row">
							<div style="margin: 0 auto">
								<button class="btn-profile btn"><v-icon name="check"></v-icon> Đã theo dõi</button>
								<button class="btn-profile btn"><v-icon name="mail"></v-icon> Nhắn tin</button>
							</div>
						</div>
					</div>
				</div>

				
			</div>
			
		</div>
		
		<div class="container">
			<h3 class="title-brand">NHỮNG BẢN THU</h3>
			<div class="row">
				<div class="col-md-3" v-for="item, index in list_record">
					<router-link :to="{ name : 'detail', params: { id: item.record_id }}" >
						<img :src="'http://media.music.kenh1.vn/' + item.record_thumb" alt="" class="img-thumbnail">
						<div class="mt-2">
							<h4>{{ item.song_name }}</h4>
						</div>
					</router-link>	
				</div>
			</div>
			
		</div>

	</div>
</template>

<script>
	export default {
		data() {
			return {
				detail : {},
				list_record : {}
			}
		},
		created() {
			this.getDetail(this.$route.params.id);
			this.getListData(this.$route.params.id)
		},
		methods: {
			getDetail(id) {
				this.$api.get('user/' + id, {
					
				}).then(res => {
					this.detail = res.data
				}).catch(e => {
					console.log(e)
				})
			},
			getListData(id) {
				this.$api.get('record', {
					params: {
						user_id_get : id,
						limit: 8
					}
				}).then(res => {
					if(res.data.status != 0) {
						this.list_record = res.data.data.items
					}
				}).catch(e => {
					console.log(e)
				})
			},
			select(id) {
				var currentUrl = window.location.origin
				window.location.href = currentUrl + '/detail/' + id
			}
		}
	}
</script>