<template>
	<section>
		<h3 class="title-brand">MV HAY NHẤT</h3>

		<div class="row">
			<div class="col-md-4 img_mv" v-for="item, index in items">
				
				<router-link :to="{ name : 'detail', params: { id: item.record_id }}">
					<img :src="'http://media.music.kenh1.vn/' + item.record_thumb" alt="" class="img-thumbnail">
					<div class="positioner">
					    <img src="./../assets/img/play.png" alt="" class="icon_play">
					</div>
				</router-link>
				<div class="mt-2 mb-4">
					<router-link :to="{ name : 'detail', params: { id: item.record_id }}">
						<h4 class="title_item">{{ item.song_name }}</h4>
					</router-link>
					<router-link :to="{ name : 'profile', params: { id: item.user_one_data.user_id }}">
						<h5>{{ item.user_one_data.name }}</h5>
					</router-link>
				</div>
					
				
			</div>
		</div>
	</section>
</template>

<script>
	export default {
		data() {
			return {
				items : ''
			}
		},
		created() {
			this.getListData();
		},
		methods: {
			getListData() {
				this.$api.get('discover/top-mv', {
					params: {
						limit : 9
					}
				}).then(res => {
					this.items = res.data.data.items
				}).catch(e => {
					console.log(e)
				})
			}
		}
	}
</script>